import { useRouter } from "next/navigation"
import { LinkButton } from "./buttons/LinkButton"
import { PrimaryButton } from "./buttons/PrimayButton";



export const AppBar = ()=>{
    const router = useRouter();
    return <div className="flex border-b justify-between ">
        <div>
            Zapier
        </div>
        <LinkButton onClick={()=>{}}>
            Contact Sales
        </LinkButton>
        <LinkButton onClick={()=>{
            router.push("/login")
        }}>
            Login
        </LinkButton>
        <PrimaryButton onClick={()=>{
            router.push("/signup")
        }}>
            Signup
        </PrimaryButton>
    </div>
}